.tableWrap {
    max-width: 715px;
    @media screen and (min-width: 768px) and (max-width: 1280px) {
      margin-top: 30px;
    }
    @media screen and (min-width: 1280px) {
      margin-top: 30px;
    }
  }
  
  .scrollTableBody {
    height: 40vh;
    overflow-x: auto;
    margin-top: 0px;
    margin-bottom: 20px;
    
  }

  .scrollTableBody tr {
    border-bottom: 1px solid rgb(189, 189, 189);
  }

  .scrollTableBody tr > td:first-child {
    width: 120px;
  }

  .scrollTableBody tr > td:nth-child(2) {
    width: 60px;
  }

  .scrollTableBody tr > td:nth-child(4) {
    overflow: overlay;
  }

  .scrollTableBody tr > td:last-child {
    width: 60px;
    font-family: var(--primary-font);
    font-weight: 400;
    font-size: 16px;
  }

  .abraCadabra {
    font-family: var(--primary-font);
    font-weight: 700;
    font-size: 18px;
  }

  .scrollTable table {
  
    width: 100%;
    table-layout: fixed;
    border-collapse: collapse;
    text-align: center;
  }
    
  .scrollTable table th {
      padding: 15px 20px;
  
      font-size: 18px;
      background-color: var(--primary-back-color);
  }
  .scrollTable table th:first-child {
        width: 120px;
        border-top-left-radius: 30px;
        border-bottom-left-radius: 30px;
  }
  .scrollTable table th:last-child {
        width: 60px;
        border-top-right-radius: 30px;
        border-bottom-right-radius: 30px;
  }

  .scrollTable table th:nth-child(2) {
    width: 60px;
  }
    
  
  .scrollTable table td {
    padding: 10px 15px;
    font-size: 16px;
  }

  /* Стилі для скрола */
::-webkit-scrollbar {
  width: 3px;
}
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.3);
}
::-webkit-scrollbar-thumb {
  box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.3);
}

/* Мобілка */

.tableWrapMob {
  max-width: 100%;
}

.scrollTableBodyMob {
  height: 60vh;
  overflow-x: auto;
  margin-top: 0px;
  margin-bottom: 20px;
}

.scrollTableMob table {
  width: 100%;
  table-layout: fixed;
  text-align: center;
  border-collapse: collapse;
  margin-bottom: 10px;
}

.scrollTableMob table tr {
  background-color: var(--primary-back-color);
}

.scrollTableMob table tr td:nth-child(2) {
  text-align: right;
  padding: 10px 15px;
}

.scrollTableMob table tr td:not(:last-child) {
  padding: 10px 15px;
  font-size: 16px;
}

.scrollTableMob table tr :not(:last-child) {
  border-bottom: 1px solid rgba(0, 0, 0, 0.308);
}


.scrollTableMob table tr > td:first-child {
  text-align: left;
  font-weight: 700;
  z-index: -1;
  left: 10px;
}

.scrollTableMob table tr :not(:first-child, :last-child) {
  box-shadow: inset 4px 0px 0px rgb(255, 102, 150);
}
.scrollTableMob table tr:first-child > td:first-child {
  border-top-left-radius: 20px;
  box-shadow: inset 4px 0px 0px rgb(255, 102, 150);
}

.scrollTableMob table tr:first-child > td:nth-child(2) {
  border-top-right-radius: 20px;
}

.scrollTableMob table tr:last-child > td:first-child {
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  box-shadow: inset 4px 0px 0px rgb(255, 102, 150);
}

.scrollTableMob table tr:last-child > td:nth-child(2) {
  border-bottom-right-radius: 20px;
}

table.tablePositive tr:not(:first-child, :last-child) {
  box-shadow: inset 4px 0px 0px rgb(36, 204, 167);
}

table.tablePositive tr:first-child > td:first-child {
  box-shadow: inset 4px 0px 0px rgb(36, 204, 167);
}

table.tablePositive tr:last-child > td:first-child {
  box-shadow: inset 4px 0px 0px rgb(36, 204, 167);
}


td.positive {
  color: rgb(36, 204, 167);
}

td.negative {
  color: rgb(255, 102, 150);
}

.scrollTableBtn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
  border-radius: 5px;
  outline: none;
  border: none;
  margin: 0 auto;
  background-color: #6e78e8;
  opacity: 0.9;
  cursor: pointer;
}

.scrollTableBtn:hover,
.scrollTableBtn:focus {
  opacity: 1;
}

.scrollTableBtnMob {
  display: block;
  width: 100%;
  height: 100%;
  padding: 10px 15px;
  border-radius: 0 0 20px 20px;
  outline: 1px solid #6e78e8;
  border: none;
  margin: 0 auto;
  color: #fff;
  background-color: #6e78e8;
  transform: scaleY(1.2);
  opacity: 0.9;
  cursor: pointer;
  font-family: var(--primary-font);
  letter-spacing: 0.4em;
}

.scrollTableBtnMob:hover,
.scrollTableBtnMob:focus {
  opacity: 1;
}